body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(240, 240, 240);
}
.home{
  margin-bottom: 50px;
}
.navigation{
  margin-left: auto;
}
.bookCard{
    display: flex;
    min-height: 200px;
    max-height: 200px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.bookCard:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

.bookCard .image{
    width: 35%;
    overflow: hidden;
}

.bookCard .image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
}

.bookCard .content{
    width: 65%;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.bookCard .content .actions{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.bookCard .content .details h2{
    font-size: 1.3rem;
}

.bookCard .content .details h3{
    font-size: 1rem;
    color: #0000008a
}
.banner{
    position: relative;
}
.banner img{
    max-width: 100%;
}

.banner-caption{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.7);
    color: #FFFFFF;
    font-size: 1.5vw;
    font-weight: 300;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

@media screen and (orientation:portrait) {
    .banner-caption{
        font-size: 5vw;
    }
}

@media (max-width: 1024px) and (orientation: landscape) {
    .banner-caption{
        font-size: 5vw;
    }
}

@media (max-width: 768px) and (orientation: landscape) {
    .banner-caption{
        font-size: 5vw;
    }
}
.container-elastic{
    width: 100%;
    max-width: 1700px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
}
h3{
    margin: 0;
}
#search-result-container{
    padding: 10px;
    margin-top: 1vw;
}

#search-result-container .loading{
    padding: 30px 0;
}

#search-result-container .no-results{
    padding: 30px 0;
    font-size: 1.5rem;
    color: #135D90;
}
